import React, { Component } from "react";
import "./navbar.css";
import styled from "styled-components";
import { Fade, Zoom } from "react-awesome-reveal";
const Hamburger = styled.div`
  cursor: pointer;
  div:nth-child(1) {
    transform-origin: 0;
    transform: ${({ toggle }) => (toggle ? "rotate(39deg)" : "rotate(0)")};
  }
  div:nth-child(2) {
    transform-origin: 0;
    transform: ${({ toggle }) => (toggle ? "translate(100%)" : "translate(0)")};
    opacity: ${({ toggle }) => (toggle ? "0" : "1")};
  }
  div:nth-child(3) {
    transform-origin: 0;
    transform: ${({ toggle }) => (toggle ? "rotate(-39deg)" : "rotate(0)")};
  }
`;
const Sidemenu = styled.div`
  transition: 0.6s ease-in-out;
  width: ${({ toggle }) => (toggle ? "100%" : "0px")};
  li a {
    animation: ${({ toggle }) => (toggle ? "menu 1s linear .1s 1" : "none")};
  }
  @keyframes menu {
    0% {
      opacity: 0;
      color: rgb(255, 78, 78);
      transform: rotateX(180deg) translate(-100%);
    }

    100% {
      opacity: 1;
      transform: translate(0);
    }
  }
`;
export default class Navbar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      toggle: false,
    };
  }

  render() {
    return (
      <div>
        <nav>
          <Fade direction='top'>
            <ul className="menu">
              <li>
                <a href=".">Home</a>
              </li>
              <li>
                <a href="#portfolio">Portfolio</a>
              </li>
              <li>
                <a href="#services">Services</a>
              </li>
              <div className="header">
                <Zoom direction='bottom'>
                  <h1>
                    <span className="firstletter">C</span>ode
                    <span className="firstletter">D</span>hruv
                  </h1>
                </Zoom>
              </div>
              <li>
                <a href="#skills">Skills</a>
              </li>
              <li>
                <a href="#about">About</a>
              </li>
              <li>
                <a href="#contactus">Contact</a>
              </li>
            </ul>
          </Fade>
          <Hamburger
            onClick={() => {
              this.setState({ toggle: !this.state.toggle });
            }}
            toggle={this.state.toggle}
            className="hamburger"
          >
            <div></div>
            <div></div>
            <div></div>
          </Hamburger>
        </nav>
        <Sidemenu toggle={this.state.toggle} className="sidemenu">
          <li>
            <a href=".">Home</a>
          </li>
          <li>
            <a href="#portfolio">Portfolio</a>
          </li>
          <li>
            <a href="#services">Services</a>
          </li>
          <li>
            <a href="#skills">Skills</a>
          </li>
          <li>
            <a href="#about">About</a>
          </li>
          <li>
            <a href="#contactus">Contact</a>
          </li>
        </Sidemenu>
      </div>
    );
  }
}
