import React, { Component } from "react";
import CountUp from "react-countup";
import "./skills.css";
import VisibilitySensor from "react-visibility-sensor";
import {Fade} from 'react-awesome-reveal'
class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      didViewCountUp: false,
    };
  }
  onVisibilityChange = (isVisible) => {
    if (isVisible) {
      this.setState({ didViewCountUp: true });
    }
  };
  render() {
    return (
      <React.Fragment>
        <VisibilitySensor onChange={this.onVisibilityChange} delayedCall>

          <Fade direction='left'>
          <div className='skill-heading' id='skills'><h1>My Skills</h1></div>
          </Fade>
        </VisibilitySensor>
        <Fade direction='top' className="Counters">
          <div className="ML">
            <CountUp
              className="count"
              end={this.state.didViewCountUp ? "93" : "0" }
              duration='6'
            />
            <span style={{fontSize:'28px',opacity:'0.5'}}>%</span>
            <p>Machine Learning</p>
          </div>
          <div className="Web">
            <CountUp
              className="count"
              end={this.state.didViewCountUp ? "97" : "0"}
              duration='6'
            />
            <span style={{fontSize:'28px',opacity:'0.5'}}>%</span>
            <p>Web Development</p>
          </div>
          <div className="Python">
            <CountUp
              className="count"
              end={this.state.didViewCountUp ? "99" : "0"}
              duration='6'
            />
            <span style={{fontSize:'28px',opacity:'0.5'}}>%</span>
            <p>Python</p>
          </div>
          <div className="Data-science">
            <CountUp
              className="count"
              end={this.state.didViewCountUp ? "98" : "0"}
              duration='6'
            />
            <span style={{fontSize:'28px',opacity:'0.5'}}>%</span>
            <p>Data Science</p>
          </div>
        </Fade>
      </React.Fragment>
    );
  }
}

export default App;
