import React, { Component } from "react";
import { Fade } from "react-awesome-reveal";
import "./contactus.css";
import * as emailjs from "emailjs-com";
export default class contactus extends Component {
  state = {
    name: "",
    email: "",
    message: "",
  };
  handleSubmit(e) {
    e.preventDefault();
    const { name, email, message } = this.state;
    let templateParams = {
      from_name: email,
      to_name: "codedhruv.ai@gmail.com",
      message_html: message + ' from ' + name,
    };
    emailjs.send(
      "gmail",
      "template_TA5sdIK8",
      templateParams,
      "user_0ixBhVzD1UPdl6d5NZJZf"
    );
    this.resetForm();
  }
  resetForm() {
    this.setState({
      name: "",
      email: "",
      message: "",
    });
    alert('Message Sent Successfully, We will reply soon')
  }
  render() {
    return (
      <React.Fragment>
        <Fade direction="left"  className="contact-header">
          Contact Me
        </Fade>
        <div className="contactme" id="contactus">
          <form className="form"  onSubmit={this.handleSubmit.bind(this)}>
            <Fade direction="right">
              <input
                type="text"
                value={this.state.name}
                onChange={e=>{this.setState({name:e.target.value})}}
                name="name"
                placeholder="Full Name"
              />
              <input
                type="email"
                value={this.state.email}
                onChange={e=>{this.setState({email:e.target.value})}}
                name="email"
                placeholder="Email"
              />
              <textarea
                name="message"
                placeholder="Enter your Message here.."
                rows="10"
                value={this.state.message}
                onChange={e=>{this.setState({message:e.target.value})}}
              ></textarea>
              <input
                className="submit-btn"
                type="submit"
                value="Send Message"
              />
            </Fade>
          </form>

          <div className="myinfo">
            <p>
              <Fade direction="left">EMAIL</Fade>
            </p>
            <h1>
              <Fade direction="left">codedhruv.ai@gmail.com</Fade>
            </h1>
            <p>
              <Fade direction="left">PHONE</Fade>
            </p>
            <h1>
              <Fade direction="left">+91 8728068766</Fade>
            </h1>
            <p>
              <Fade direction="left">ADDRESS</Fade>
            </p>
            <h1>
              <Fade direction="left">Sector 14, Chandigarh(160014), India</Fade>
            </h1>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
